html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 97%;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Palanquin Dark", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #4f4f4f;
  line-height: 1.8;
}

/** Media Queries **/
.login {
  margin-top: 5rem;
}
.login .uptask {
  margin-bottom: 0;
  background: linear-gradient(to right, #21b26f 0%, #84e8ba 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 9rem;
  }
}
.login .tagline {
  text-align: center;
  margin-top: 1rem;
}
.login input[type=submit] {
  background-color: #00cc88;
  padding: 1.5rem 4rem;
  color: white;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #009966;
  cursor: pointer;
}
.login .acciones a {
  color: #115c39;
}

.formulario {
  margin-top: 3rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input[type=text], .formulario .campo input[type=email], .formulario .campo input[type=password] {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #e5e5e5;
}
.formulario .campo input[type=date] {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: white;
}
.formulario .campo select {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: white;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  color: #000;
  margin-bottom: 2rem;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}
.acciones a:hover {
  color: gray;
}

.alerta {
  padding: 1rem;
  text-transform: uppercase;
  margin-top: 1rem;
  font-size: 1.4rem;
  max-width: 80rem;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
}
.alerta.error {
  background-color: #ffe6e6;
  border-left: 0.5rem solid #cc0000;
  color: #cc0000;
}
.alerta.error button {
  cursor: pointer;
  background-color: transparent;
  color: #cc0000;
}
.alerta.error button:hover {
  background-color: #ffcccc;
}
.alerta.exito {
  background-color: #c4f4de;
  border-left: 0.5rem solid #198754;
  color: #198754;
}
.alerta.exito button {
  cursor: pointer;
  background-color: transparent;
  color: #198754;
}
.alerta.exito button:hover {
  background-color: #aff0d2;
}

@media (min-width: 768px) {
  .tabs {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .tabs {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(5, 1fr);
  }
}
.tabs .botones {
  background-color: #198754;
  border: solid 1px white;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  font-weight: normal;
  transition: background-color 300ms ease;
}
.tabs .botones:hover {
  cursor: pointer;
  background-color: #006644;
}
.tabs .botones.actual {
  background-color: #FFFFFF;
  color: #009966;
}

.paginacion button {
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}

.ocultar-paginacion {
  visibility: hidden;
}

.boton {
  background-color: #009966;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #006644;
  cursor: pointer;
}

.boton-crear-colecta {
  background-color: #198754;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .boton-crear-colecta {
    width: auto;
  }
}
.boton-crear-colecta:hover {
  background-color: #115c39;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cc0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #990000;
  cursor: pointer;
}

.boton-actualizar {
  background-color: #198754;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-actualizar {
    width: auto;
  }
}
.boton-actualizar:hover {
  background-color: #115c39;
  cursor: pointer;
}

.boton-cerrar-session {
  background-color: #334a64;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.4rem;
  padding: 1rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .boton-cerrar-session {
    width: auto;
  }
}
.boton-cerrar-session:hover {
  background-color: #223142;
  cursor: pointer;
}

.dashboard {
  background-color: #e8e8e8;
}
@media (min-width: 768px) {
  .dashboard {
    height: 100vh;
    display: flex;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .nombre-pagina {
  text-align: center;
  font-size: 2.7rem;
  margin-top: 3rem;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}

.barra-mobile {
  background-color: #223142;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  margin: 0;
  color: #FFFFFF;
}
.barra-mobile img {
  width: 3rem;
}
.barra-mobile img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    background-color: #FFFFFF;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p {
  margin: 0;
  font-size: 1.4rem;
}
.barra p span {
  font-weight: bold;
  color: #198754;
}

.sidebar {
  opacity: 0;
  background-color: #223142;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.95);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar.ocultar {
    height: 100%;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
  }
}
.sidebar h2 {
  padding: 2rem 9rem 1rem 2rem;
  text-align: left;
  color: #FFFFFF;
  font-size: 6rem;
}
.sidebar .sidebar-nav {
  margin-top: 4rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  font-size: 1.7rem;
  padding: 1.4rem 0 1.4rem 2rem;
  transition-property: background-color;
  transition-duration: 100ms;
}
.sidebar .sidebar-nav a:nth-last-child(even):not(:first-of-type) {
  border-bottom: 1px solid #84e8ba;
}
.sidebar .sidebar-nav a:hover:not(:first-of-type), .sidebar .sidebar-nav a.activo:not(:first-of-type) {
  background-color: #136941;
  border-left: 0.5rem solid #009966;
  color: #FFFFFF;
}
.sidebar .crear-colecta {
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
  padding: 0;
}
.sidebar .crear-colecta span {
  background-color: #009966;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  display: inline;
  border-radius: 2rem;
  padding: 1rem 2rem;
}
@media (min-width: 768px) {
  .sidebar .crear-colecta span {
    width: auto;
  }
}
.sidebar .crear-colecta span:hover {
  background-color: #006644;
  cursor: pointer;
}
.sidebar .cerrar-session-mobile {
  margin-top: 5rem;
}
.sidebar .cerrar-session-mobile a {
  background-color: red;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-session-mobile a {
    width: auto;
  }
}
.sidebar .cerrar-session-mobile a:hover {
  background-color: #cc0000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-session-mobile {
    display: none;
  }
}

.no-colectas a {
  color: #198754;
}

.listado-colectas {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-colectas {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-colectas {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-colectas .colecta {
  background-color: #1d9d61;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 1.5;
  border-radius: 0.4rem;
  color: #FFFFFF;
}
.listado-colectas .colecta:hover {
  background-color: #157147;
}

.seccion {
  margin-top: 2rem 0;
  display: none;
  overflow: scroll;
}
.seccion .indicaciones {
  text-align: center;
}
.seccion h2 {
  margin-top: 1rem;
  font-size: 3rem;
}
.seccion h3 {
  font-size: 2.7rem;
}

.mostrar-nav {
  display: block;
}

.ocultar-nav {
  display: none;
}